<template>
  <div class="w-container">
    <div class="section-2"
         v-for="(car, index) in cars"
         :key="index"
         v-loading="loading">
      <div>
        <img src="https://uploads-ssl.webflow.com/5bf5afa9ba0bd51e495041ce/5bf9871b4ae342456f82bae9_peugeot-208-5d-schwarz-2012.png"
             alt=""
             class="image-2"
        >
      </div>
      <div class="car-info">
        <h3 class="heading">{{ car.brand }} {{ car.model }}</h3>
        <p>autre info</p>
        <el-rate
          :value="Math.round(car.rate.toFixed(1) * 100) /100"
          v-on:input="car.rate = $event.target.value"
          disabled
          show-score
          text-color="#ff9900"
          score-template="{value} / 5">
        </el-rate>
      </div>
      <div class="price">
        <div class="price-name">
          <h3 class="heading-2">MEILLEUR PRIX - MODIFICATION &amp; ANNULATION </h3>
        </div>
        <div class="price-value">
          <div class="div-block-4">
            <div><strong>{{ car.priceRent }}€/jour TTC</strong></div>
            <div class="text-block-2">soit 640,42€</div>
          </div>
          <div class="div-block-3">Louer ce modèle</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Rent',
  data() {
    return {
      cars: [],
      loading: true,
    };
  },
  mounted() {
    axios.get('http://localhost:8080/RentalCarsUPEM/api/cars')
      .then((response) => {
        this.cars = response.data;
        this.loading = false;
      })
      .catch((error) => {
        console.log(error);
        this.loading = false;
        this.$notify.error({
          title: 'Erreur',
          message: 'Une erreur est survenue',
        });
      });
  },
};
</script>

